.home{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #d7dbd6;
    z-index: 100;
    position: relative;

}
.home::before{
    width: 100vw;
    height: 222px;
    background-color: #0c2369d8;
    position: absolute;
    z-index: -1;
    content: "";
    top: 0;
}
.home-container{
    width: 100%;
    height: 100%;
    max-width: 1366px;
    z-index: 100;
    display: flex;
}
.home-bg{
    flex: 1;
    background-color: #fafafa;
    z-index: 100;
    height: 100%;
    display: flex;
    align-items:center;
    justify-content: center;
}
.home-bg > img{
    width: 300px;
    margin: auto;
}
@media onlyscreen and (min-width:1376px){
    .home-container{
        height: 95%;
    }
}